<template>
    <div class="container">
        <div class="header"> 
            <div class="top">
                <router-link :to="'alllist'"><img class="logo" src="../../../assets/img/logo-new.png" alt="AcFun" title="AcFun"></router-link>
                <el-input class="search" placeholder="请输入内容" v-model="search">
                    <el-select class="select" v-model="select" slot="prepend" placeholder="请选择">
                        <el-option label="文章" value="article"></el-option>
                        <el-option label="用户" value="user"></el-option>
                    </el-select>
                    <el-button slot="append" icon="search"></el-button>
                </el-input>
                <el-dropdown trigger="click" menu-align="start" class="dropdown" @command="userEdit">
                    <el-button type="primary" size="small">
                        {{userinfo.username}}<i class="el-icon-caret-bottom el-icon--right"></i>
                    </el-button>
                    <el-dropdown-menu slot="dropdown">
                        <el-dropdown-item command="userinfo">个人设置</el-dropdown-item>
                        <el-dropdown-item command="logout">退出登录</el-dropdown-item>
                    </el-dropdown-menu>
                </el-dropdown>
                <router-link to="personlist"><img class="poster" :src="userinfo.poster" alt=""></router-link>
            </div>
        </div>
        <div class="article">
            <router-view></router-view>
        </div>
    </div>                    
</template>
<script>
    var {
        mapGetters,
        mapActions
    } = Vuex
    export default{
        data(){
            return{
                select:'',
                search:''
            }
        },
        mounted(){
            if(!localStorage.getItem('token')){
                this.$message.error('请登陆后再进入');
                setTimeout(()=>{
                    window.location.href='http://localhost:4000';
                },1000)
            };
            this.getuserinfo();
        },
        computed:{
            ...mapGetters({
                userinfo:'userinfo'
            })
        },
        methods:{
            ...mapActions({
                getuserinfo:'getuserinfo'
            }),
            userEdit(edit){
                switch(edit){
                    case 'logout':
                        localStorage.removeItem('token');
                        window.location.href='http://localhost:4000';
                        break;
                    case 'userinfo':
                        this.$router.push('/userinfo');
                        break;
                    default:
                        break;
                }
            },
        }
    }
</script>
<style lang="less" scoped>
    .container{
        position:relative;
        width:100%;
        height:auto;

        .header{
            width:100%;
            height:52px;
            border-bottom:1px #FFA5C7 solid;

            .top{
                width:980px;
                height:100%;
                margin:auto;
                text-align:center;
            
                .logo{
                    float:left;
                    padding:10px 0 10px;
                }
                .poster{
                    float:right;
                    max-width:50px;
                    max-height:50px;
                    border-radius:25px;
                }
                .dropdown{
                    float:right;
                    cursor:pointer;
                    margin-left:20px;
                    line-height:50px;
                }
                .search{
                    width:500px;
                    margin-top:5px;

                    .select{
                        width:100px;
                    }
                }
            }
        }

        .article{
            width:980px;
            margin:auto;
        }
    }
</style>


